body {
	background: #2980b9;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #2980b9, #2c3e50);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #2980b9, #2c3e50);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#uploadForm{
	display: flex;
	justify-content: center;
}

.filepond--root {
	width: 60%;
	max-width: 800px;
}

.filepond--root .filepond--credits[style] {
	display: none;
}

.filepond--panel-root {
	background-color: #eee;
}
.container {
	margin-top: 60px;
	transition: height 0.5s ease-in-out;
}

#response-container {
	visibility: hidden;
	height: 0;
	background: #f1f0ef;
	margin: 4rem auto;
	padding: 15px 20px;
	border-radius: 12px;
	width: 80%;
	color: #4f4f4f;
	max-width: 1100px;
	transition: height 1s ease-in-out, visibility 1s ease-in-out;
}

.active {
	visibility: visible !important;
	height: auto !important;
	animation: fadeIn 1s;
}

#response-text {
	font-size: 1.2rem;
	line-height: 1.6;
	font-family: 'PT Sans', sans-serif;
}

@keyframes fadeIn {
	from {
		visibility: 0;
		transform: translateY(-20px);
	}
	to {
		visibility: 1;
		transform: translateY(0);
	}
}